<template>
    <div class="prddetail-contain">
        <h3 class="prddetail-title"><span class="title-leftline"></span>{{moreQuotation.prddetailTitle}}</h3>
        <div class="product-contain border-top">
            <div class="prddetail-content" v-for="(list, index) in moreQuotation.lists" :key="index + 'content_'" :class="{'border-bottom' :index == 0}">
                <router-link to="/onedaytour/detail">
                    <div class="productlist-item">
                        <h3 class="productprices-name">{{list.productpricesName}}</h3>
                        <p class="productprices-supplier"><span class="supplier-qunar" v-show="list.supplierqunarShow"></span>{{list.productpricesSupplier}}</p>
                        <div class="producttag-content">
                            <p class="producttag-item border" v-show="list.producttagShow" v-html="list.producttagItem"></p>
                        </div>
                        <div class="product-tag">
                            <p class="product-tag-item" v-for="item in list.productTag"> <span class="iconfont desctag" v-html="item.icon"></span>{{item.productDesctag}}
                            </p>
                        </div>
                    </div>
                    <div class="productprices-details">
                        <i class="price">&yen;</i><span class="price-num">{{list.productpricesDetails.priceNum}}</span>
                        <span class="iconfont product-arrow" v-html="list.productpricesDetails.icon"></span>
                    </div>
                </router-link>
            </div>
        </div>
        <div class="price-bottom"></div>
    </div>

</template>

<script>
    export default {

        props: ["moreQuotation"]

    }

</script>


<style scoped>
    .prddetail-contain {
        background: #ffffff;
        font-size: 0;
    }

    .prddetail-title {
        height: .84rem;
        padding-left: .24rem;
        line-height: .84rem;
        font-size: .32rem;
        color: #212121;
    }

    .title-leftline {
        float: left;
        display: block;
        width: .06rem;
        height: .28rem;
        background: #00bcd4;
        margin: .3rem .12rem 0 0;
    }

    .product-contain {
        padding: 0 .24rem;
    }

    .product-contain::before {
        border-color: #f0f0f0;
    }

    .prddetail-content {
        position: relative;
        padding: .26rem 0 .28rem;
        overflow: hidden;
    }

    .prddetail-content::before {
        border-color: #f0f0f0;
    }

    .productlist-item {
        float: left;
        width: 5.4rem;
    }

    .productprices-name {
        font-size: .28rem;
        color: #212121;
        line-height: .4rem;
    }

    .productprices-supplier {
        font-size: .28rem;
        color: #9e9e9e;
        height: .28rem;
        line-height: .28rem;
        margin: .1rem 0;
    }

    .supplier-qunar {
        display: inline-block;
        width: .3rem;
        height: .3rem;
        vertical-align: -.06rem;
        margin-right: .06rem;
        background: url(data:image/png;base64,) 0 0 no-repeat;
        background-size: 100% 100%;
    }

    .producttag-item {
        width: .94rem;
        height: .28rem;
        font-size: .24rem;
        color: #ff124f;
        text-align: center;
        line-height: .28rem;
        margin-bottom: .1rem;
    }

    .producttag-item::before {
        border-color: #ff1200;
    }

    .desctag {
        font-size: .2rem;
        color: #77cddd;
        margin-right: .1rem;
    }

    .product-tag {
        overflow: hidden;
    }

    .product-tag-item {
        float: left;
        font-size: .24rem;
        color: #616161;
        margin-right: .32rem;
    }

    .productprices-details {
        position: absolute;
        top: 50%;
        right: .1rem;
        height: .32rem;
        line-height: .32rem;
        margin-top: -.16rem;
    }

    .price {
        font-size: .18rem;
        color: #ff8300;
    }

    .price-num {
        font-size: .32rem;
        color: #ff8300;
        margin-left: .08rem;
    }

    .product-arrow {
        font-size: .28rem;
        color: #9e9ead;
        margin-left: .08rem;
    }

    .price-bottom {
        height: 1.2rem;
        background: #f5f5f5;
    }
</style>


